import React, { FC } from 'react';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchExpression,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { CLASS_NAME, EventoMHO } from '../../../../models/dtos/mho/eventoMHO';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'codigo',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const EventoMHOSearchView: FC<SearchViewProps<EventoMHO>> = props => {
  const columns: ColumnsProps[] = [
    {
      dataField: 'codigo',
      editable: false,
      filter: {
        type: FilterType.NUMBER,
      },
      sort: true,
      text: 'Código',
    },
    {
      dataField: 'descricao',
      editable: true,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Descrição',
    },
    {
      dataField: 'tipo',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Tipo',
    },
    {
      dataField: 'valor',
      editable: true,
      filter: {
        type: FilterType.NUMBER,
      },
      sort: true,
      text: 'Valor',
    },
  ];

  function getSearchExpression(): SearchExpression[] {
    const expressions: SearchExpression[] = [];

    expressions.push({
      fieldName: 'finalidade',
      value: 'manual',
    });

    return expressions;
  }

  return (
    <WindowComponent title="Manutenção de Eventos" wikiId={2029}>
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'EventoMHOEditView', CLASS_NAME)}
        searchExpressions={getSearchExpression}
      />
    </WindowComponent>
  );
};

export default EventoMHOSearchView;
